import Vue from 'vue'
import VueRouter from 'vue-router'
import SignIn from '../views/LetsIn/SignIn/Index.vue'
import SignUp from '../views/LetsIn/SignUp/Index.vue'
import LetsIn from '../views/LetsIn/Lets_In/Index.vue'
import FillProfile from '../views/Account_setup/Fill_Profile/index.vue'
import NewPin from '../views/Account_setup/New_PIN/index.vue'
import ForgotPassword from '../views/Forgot_resetPassword/Forgot_password/index.vue'
import VerifyPassword from '../views/Forgot_resetPassword/Forgot_password/verify_password.vue'
import NewPassword from '../views/Forgot_resetPassword/Reset_password/index.vue'
import Home from '../views/Home_actionMenu/Home/index.vue'
import ShoesType from '../views/Home_actionMenu/Shoes_type/index.vue'
import ProductDetail from '../views/Home_actionMenu/Product_detail/index.vue'
import Notifications from '../views/Home_actionMenu/Notification/index.vue'
import MyWishlist from '../views/Home_actionMenu/My_wishlist/index.vue'
import Popular from '../views/Home_actionMenu/Popular/index.vue'
import Offers from '../views/Home_actionMenu/Special_offers/index.vue'
import Search from '../views/Home_actionMenu/Search/index.vue'
import MyCart from '../views/Cart_orderCheckout/My_cart/index.vue'
import Checkout from '../views/Cart_orderCheckout/Checkout/index.vue'
import ShippingAddress from '../views/Cart_orderCheckout/Shipping_address/index.vue'
import ChooseShipping from '../views/Cart_orderCheckout/Choose_shipping/index.vue'

Vue.use(VueRouter)

const routes = [
  {
    path : '/',
    redirect: 'home'
  },
  {
    path: '/lets-in',
    name: 'lets-in',
    component: LetsIn
  },
  {
    path: '/sign-in',
    name: 'sign-in',
    component: SignIn
  },
  {
    path: '/sign-up',
    name: 'sign-up',
    component: SignUp,
    // children: [
    //   {
    //     path: '/fill-profile',
    //     name: 'fill-profile',
    //     component: FillProfile
    //   }
    // ]
  },
  {
    path: '/fill-profile',
    name: 'fill-profile',
    component: FillProfile
  },
  {
    path: '/new-pin',
    name: 'new-pin',
    component: NewPin
  },
  {
    path: '/forgot-password',
    name: '/forgot-password',
    component: ForgotPassword
  },
  {
    path: '/verify-password',
    name: '/verify-password',
    component: VerifyPassword
  },
  {
    path: '/new-password',
    name: '/new-password',
    component: NewPassword
  },
  {
    path: '/home',
    name: '/home',
    component: Home
  },
  {
    path: '/type',
    name: '/type',
    component: ShoesType
  },
  {
    path: '/product-detail',
    name: '/product-detail',
    component: ProductDetail
  },
  {
    path: '/notifications',
    name: '/notifications',
    component: Notifications
  },
  {
    path: '/my_wishlist',
    name: '/my_wishlist',
    component: MyWishlist
  },
  {
    path: '/popular',
    name: '/popular',
    component: Popular
  },
  {
    path: '/offers',
    name: '/offers',
    component: Offers
  },
  {
    path: '/search',
    name: '/search',
    component: Search
  },
  {
    path: '/my_cart',
    name: '/my_cart',
    component: MyCart
  },
  {
    path: '/checkout',
    name: '/checkout',
    component: Checkout
  },
  {
    path: '/shipping_address',
    name: '/shipping_address',
    component: ShippingAddress
  },
  {
    path: '/choose_shipping',
    name: '/choose_shipping',
    component: ChooseShipping
  }
  
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
